/*--------------------------------------------*/
/* reset */
/*--------------------------------------------*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*--------------------------------------------*/
/* common */
/*--------------------------------------------*/
html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  color: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #EFEFEF;
}

canvas {
  display: block;
  top: 0;
  left: 0;
}

#banner {
  /* bigbox */
  width: 300px;
  height: 250px;
  overflow: hidden;
  position: relative;
  background-color: #0042b0;
  -webkit-text-size-adjust: 100%;
  opacity: 1;
  font-family: blk;
}

canvas, .text, .frame, .outline, img {
  position: absolute;
}

img {
  vertical-align: top;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1, 1);
}

.outline {
  z-index: 99;
  width: inherit;
  height: inherit;
  border: 1px solid black;
  pointer-events: none;
  position: absolute;
}

.frame {
  width: inherit;
  height: inherit;
  opacity: 0;
  top: 0;
  left: 0;
}

p {
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

sup {
  font-size: 50%;
  vertical-align: top;
  position: relative;
  left: -3px;
}

.shadow {
  -webkit-filter: drop-shadow(5px 5px 9px black);
  filter: drop-shadow(5px 5px 9px black);
}

@font-face {
  font-family: 'blk';
  src: url(data:application/font-woff;charset=utf-8;base64,) format("woff");
  font-weight: normal;
  font-style: normal;
}

#banner {
  font-size: 20px;
  width: 300px;
  height: 250px;
}

#cans {
  width: 105px;
  height: 202px;
  -webkit-transform: translate(177px, 25px);
  -moz-transform: translate(177px, 25px);
  -ms-transform: translate(177px, 25px);
  transform: translate(177px, 25px);
  position: absolute;
  overflow: hidden;
}

#title {
  -webkit-transform: translate(20px, 47px);
  -moz-transform: translate(20px, 47px);
  -ms-transform: translate(20px, 47px);
  transform: translate(20px, 47px);
  position: absolute;
  font-size: 50px;
  width: 132px;
  line-height: 40px;
  letter-spacing: 1.3px;
}

#title span.you {
  position: relative;
  left: 3px;
}

#title span.y {
  margin-right: -4px;
}

#sub {
  -webkit-transform: translate(21px, 188px);
  -moz-transform: translate(21px, 188px);
  -ms-transform: translate(21px, 188px);
  transform: translate(21px, 188px);
  position: absolute;
  font-size: 13px;
  color: white;
}

#legal {
  -webkit-transform: translate(19px, 240px);
  -moz-transform: translate(19px, 240px);
  -ms-transform: translate(19px, 240px);
  transform: translate(19px, 240px);
  position: absolute;
  font-size: 5px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: white;
}
